<template lang="pug">
  div
    .title 使用閉包串接回呼函式
    Button(@click="ShowDailog") 顯示彈窗
    .bg
      pre {{code}}


</template>

<script>
export default {
  data() {
    return {
      code: `this.$dialog()
  .onOk(() => {
    alert("點擊了確認");
  })
  .onCancel(() => {
    alert("點擊了取消");
  });
`
    };
  },
  methods: {
    ShowDailog() {
      this.$dialog()
        .onOk(() => {
          alert("點擊了確認");
        })
        .onCancel(() => {
          alert("點擊了取消");
        });
    }
  }
};
</script>

